/* App.css */
body {
  font-family: Arial, sans-serif;
  background: linear-gradient(to right, #00c6ff, #0072ff);
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: white;
}

.login-container {
  background: white;
  border-radius: 10px;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  max-width: 400px;
  width: 100%;
  color: #333;
}

h1 {
  font-size: 2rem;
  margin-bottom: 1.5rem;
}

.login-button,
.line-login-button,
.register-button,
.back-button {
  background: #00c6ff;
  border: none;
  padding: 1rem 1.5rem;
  color: white;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
  margin: 0.5rem;
}

.login-button:hover,
.line-login-button:hover,
.register-button:hover,
.back-button:hover {
  background: #0072ff;
}

.profile-box img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 1rem;
}

.profile-box h2 {
  margin: 0.5rem 0;
}

.profile-box p {
  margin: 0.5rem 0;
}

.email-register-box .form-group {
  margin-bottom: 1.5rem;
}

.email-register-box label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #555;
}

.email-register-box input {
  width: 100%;
  padding: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  color: #333;
  transition: border-color 0.3s;
}

.email-register-box input:focus {
  outline: none;
  border-color: #00c6ff;
}
